<template>
  <li @click="select">
    <el-tag :class="'el-tag--blue transaction-type type-' + transferTypeTitle">
      {{ transferTypeTitle }}
    </el-tag>
    <div class="details">
      <p class="label">
        {{ transaction.title }}
      </p>
      <p class="value" v-if="transaction.status == 'pending'">
        {{ $t('RCD_pending') }}
      </p>
      <p class="value" v-else>
        {{ transaction.txnDate | date("MMM d, yyyy 'at' h:mm aa") }}
      </p>
    </div>
    <p
      :class="
        'amount ' +
          (transaction.status == 'pending'
            ? 'pending'
            : transaction.txnType === 'debit'
              ? 'minus'
              : 'plus')
      ">
      {{ transaction.amount | transactionAmount(getSelectedAccount.currency) }}
    </p>
  </li>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
import { transferType } from '../enums/index';
export default {
  name: 'TransactionComponent',
  props: {
    // eslint-disable-next-line
    transaction: Object
  },
  computed: {
    ...mapGetters('account', ['getSelectedAccount']),
    transferTypeTitle() {
      if(this.transaction.subType === 'reward' && this.transaction.transferType === 'card') {
        return transferType['titles']['reward'];
      } else {
        return transferType['titles'][this.transaction.transferType] || this.transaction.transferType;
      }
    }
  },
  methods: {
    ...mapMutations('transaction', ['setSelectedTransaction']),
    select() {
      window.analytics && window.analytics.track('homeTransactionsDetails');
      this.setSelectedTransaction(this.transaction);
      this.$emit('showDetails');
    }
  }
};
</script>
